import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import About from "./components/About";
import Header from "./layout/Header";
import rightArrow from "./images/right-arrow.svg";
import "./App.css";

class App extends React.Component {
  state = {
    color: null,
    colorName: null,
    favouritesOpen: false,
    favourites: []
  };

  numGen = () => {
    let val = Math.floor(Math.random() * 255);
    return val;
  };

  // Create a new rgb value and color name and set state
  newColor = async () => {
    let num1 = this.numGen();
    let num2 = this.numGen();
    let num3 = this.numGen();
    let color = `rgb(${num1}, ${num2}, ${num3})`;

    let response = await fetch(`https://www.thecolorapi.com/id?rgb=${color}`);
    let data = await response.json();
    let colorName = data.name.value;

    this.setState({
      color: color,
      colorName: colorName
    });
  };

  // Get random color on page load, load favourites
  componentDidMount() {
    this.newColor();
    this.getLS();
  }

  // Add color RGB value to clipboard
  copyColor = colorVal => {
    let input = document.createElement("input");
    input.setAttribute("value", colorVal);
    document.body.appendChild(input);
    input.select();
    document.execCommand("copy");
    document.body.removeChild(input);
  };

  // Add to Favourites
  starColor = () => {
    if (
      this.state.favourites.filter(obj => obj.rgb === this.state.color)
        .length === 0 &&
      this.state.favourites.length < 10
    ) {
      this.setState(
        {
          favourites: [
            ...this.state.favourites,
            {
              rgb: this.state.color,
              colorName: this.state.colorName
            }
          ]
        },
        this.updateLS
      );
    }
  };

  // Delete from Favourites
  deleteItem = id => {
    this.setState(
      {
        favourites: [...this.state.favourites.filter(fav => fav.rgb !== id)]
      },
      () => {
        if (this.state.favourites.length === 0) {
          this.setState({ favouritesOpen: false });
        }
        this.updateLS();
      }
    );
  };

  // Toggle Favourites menu open/close
  menuToggle = () => {
    if (this.state.favourites.length !== 0) {
      this.setState({
        favouritesOpen: !this.state.favouritesOpen
      });
    }
  };

  // Retrieve items from LS
  getLS = () => {
    let data = [];

    if (localStorage.getItem("colors") !== null) {
      data = JSON.parse(localStorage.getItem("colors"));
    }

    this.setState({ favourites: data });
  };

  // Add current starred items to LS
  updateLS = () => {
    if (this.state.favourites.length !== 0) {
      localStorage.setItem("colors", JSON.stringify(this.state.favourites));
    } else {
      localStorage.removeItem("colors");
    }
  };

  render() {
    return (
      <Router>
        <div>
          <Header />
          <Route
            exact
            path="/"
            render={() => (
              <>
                <div
                  className="container"
                  style={{ background: this.state.color }}
                >
                  <div
                    className="title__box"
                    onClick={() => this.copyColor(this.state.color)}
                  >
                    <p className="title__color-name">{this.state.colorName}</p>
                    <p className="title__color-value">{this.state.color}</p>
                  </div>

                  <div className="buttons">
                    <button
                      id="generate"
                      className="generate-btn"
                      onClick={this.newColor}
                    >
                      New Colour
                    </button>
                    <button id="star" onClick={this.starColor}>
                      Add to Favourites
                    </button>
                  </div>

                  <div
                    className={`favourites ${
                      this.state.favouritesOpen ? "open" : ""
                    }`}
                  >
                    <div
                      id="f-toggle"
                      className="f-toggle"
                      onClick={this.menuToggle}
                    >
                      <img src={rightArrow} alt="" />
                    </div>
                    <div id="list">
                      <ul id="list-inner" className="list-inner">
                        {this.state.favourites.map(
                          fav => (
                            <li
                              className="fav-color"
                              data-color={fav.rgb}
                              style={{
                                background: fav.rgb
                              }}
                              onClick={() => this.copyColor(fav.rgb)}
                              key={fav.rgb}
                            >
                              <span
                                className="remove"
                                onClick={() => this.deleteItem(fav.rgb)}
                              />
                              {`${fav.colorName}: 
                            ${fav.rgb}`}
                            </li>
                          ),
                          this
                        )}
                      </ul>
                    </div>
                  </div>
                </div>
              </>
            )}
          />
          <Route path="/about" component={About} />
        </div>
      </Router>
    );
  }
}

export default App;
